.preview {
  &-bottom {
    gap: 3rem;
  }
  &__box {
    background-color: $primary;
    border-radius: $border-radius-lg;
    color: $white;
    padding: 3.5rem;

    &-1 {
      margin-top: 0;
    }
    &-2 {
      margin-top: $margin-top-sm;
    }
    &-3 {
      margin-top: $margin-top-md;
    }

    & p {
      margin: 0;

      &:first-child {
        color: $white;
      }

      &:last-child {
        color: $secondary;
      }
    }
  }

  &__photo {
    max-width: 65%;
    height: auto;
  }
}

@media screen and (max-width: 992px) {
  .preview {
    &-bottom {
      gap: 1rem;
    }
  }
}
